import { PUBLIC_IMG_PATH } from '../js/util';
import './Nav.scss'
import { NavLink } from 'react-router-dom';

const Nav = (props) => {
    const { setShowDetail } = props;

    return (
        <>
            {
                setShowDetail
                    ? <div className='nav-logo-area-in-detail' onClick={() => setShowDetail(false)}><img className='logo' src={`${PUBLIC_IMG_PATH}/studioiwill-main-logo.png`} alt='' /></div>
                    : <div className='nav-container'>
                        <NavLink to={'/'} className='nav-logo-area'><img className='logo' src={`${PUBLIC_IMG_PATH}/studioiwill-small-logo.png`} alt='' /></NavLink>
                        <div className='menu-area'>
                            <NavLink to={'/about'} className='menu-item'>~ </NavLink>
                            <NavLink className='menu-item' to={'/'}></NavLink>
                            <div className='menu-item' onClick={() => window.location.href = 'mailto:designstudio.iwill@gmail.com'}></div>
                            <a className='menu-item' href={'https://instagram.com/design.studio.iwill?igshid=MjEwN2IyYWYwYw=='} target='_blank' rel="noreferrer"><img className='instagram' src={`${PUBLIC_IMG_PATH}/instagram-logo.webp`} alt='' /></a>
                        </div>
                    </div>
            }
        </>
    )
}

export default Nav